/* =========================================================
   CLEANED + OPTIMIZED SKY-BLUE GLASSMORPHIC STYLESHEET
   (Keeps .container and .admin-container-vertical separate)
========================================================= */

/* ------------------------
   THEME VARIABLES
--------------------------- */
:root{
  --bg-1: #eef7ff;           /* page background top */
  --bg-2: #f7fbff;           /* page background bottom */
  --panel: rgba(255,255,255,0.78);
  --panel-strong: rgba(255,255,255,0.88);
  --glass-border: rgba(150,190,255,0.45);
  --muted: #6c7a89;
  --primary: #0b74d4;
  --primary-deep: #045ca6;
  --accent: #56b7ff;
  --sky-1: #eaf6ff;
  --sky-2: #dff3ff;
  --radius-lg: 14px;
  --radius-md: 10px;
  --radius-sm: 8px;
  --glass-blur: 10px;
  --shadow-blue-strong: 0 20px 50px rgba(80,150,255,0.18);
  --shadow-blue-soft: 0 8px 30px rgba(80,150,255,0.12);
  --shadow-neutral: 0 6px 18px rgba(10,20,40,0.06);
  --max-width: 1400px;
  --content-padding: 24px;
  --trans-fast: 160ms;
  --trans-medium: 220ms;
}

/* =========================================================
   GLOBAL
========================================================= */
* { box-sizing: border-box; -webkit-font-smoothing:antialiased; -moz-osx-font-smoothing:grayscale; }
html, body, #root { height: 100%; }
body {
  font-family: "Poppins", "Inter", Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(180deg, var(--bg-1), var(--bg-2));
  color: #15232b;
  -webkit-font-smoothing: antialiased;
  font-size: 15px;
  line-height: 1.45;
  -webkit-text-size-adjust: 100%;
}

/* convenience */
.container {
  padding: var(--content-padding);
  max-width: var(--max-width);
  margin: 20px auto;
  width: 100%;
}
.app-container { min-height: 100vh; display: flex; flex-direction: column; }
.hidden { display:none !important; }
.text-muted { color: var(--muted); font-size: 13px; }

/* links */
a { color: inherit; text-decoration: none; }
a:hover { text-decoration: underline; }

/* =========================================================
   HEADER (GLASS + SKY TINT)
========================================================= */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;     /* force fixed slim height */
  padding: 0 28px;  /* remove vertical padding */
  background: #f7fbff;
  border-bottom: 1px solid #e5e9f0;
  position: sticky;
  top: 0;
  z-index: 1100;
}

/* Logo section */
.header .logo {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header .logo img {
  height: 50px;     /* smaller logo */
  width: 32px;
  border-radius: 6px;
}

.header .logo h1 {
  font-size: 16px;
  font-weight: 700;
  margin: 0;
}

/* Navigation */
.nav-links {
  display: flex;
  align-items: center;
  gap: 18px;
}

.header-avatar {
  width: 50px;        /* smaller profile avatar */
  height: 50px;
  border-radius: 50%;
  border: 2px solid #ddd;
  object-fit: cover;
}

.nav-links a {
  color: #063b66;
  font-weight: 600;
  padding: 6px 8px;
  border-radius: 8px;
  transition: 0.2s ease;
}

.nav-links a:hover {
  transform: translateY(-2px);
  background: rgba(80,150,255,0.10);
  box-shadow: 0 8px 28px rgba(80,150,255,0.10);
}

/* PROFILE */
.profile-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.header-avatar:hover {
  transform: scale(1.05);
}

.profile-dropdown {
  position: absolute;
  top: 48px;
  right: 0;
  background: white;
  width: 180px;
  padding: 12px;
  border-radius: 10px;
  box-shadow: 0px 4px 20px rgba(0,0,0,0.15);
  animation: fadeIn 0.2s ease;
  z-index: 999;
}

.dropdown-name {
  font-weight: bold;
  margin-bottom: 8px;
  color: #333;
}

.dropdown-item {
  display: block;
  padding: 8px 10px;
  border-radius: 6px;
  text-decoration: none;
  color: #333;
  margin-bottom: 5px;
  transition: 0.2s;
}

.dropdown-item:hover {
  background: #f3f3f3;
}

.logout-btn {
  background: #ff4f4f;
  color: white;
  border: none;
  width: 100%;
  text-align: left;
  padding: 8px 10px;
  border-radius: 6px;
  cursor: pointer;
}

.logout-btn:hover {
  background: #d63a3a;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}


/* =========================================================
   CARD / PANELS (CLEANED)
========================================================= */
.card {
  background: linear-gradient(180deg, rgba(255,255,255,0.92), rgba(245,252,255,0.95));
  padding: 22px;
  border-radius: var(--radius-lg);
  margin-top: 18px;
  border: 1px solid rgba(150,190,255,0.28);
  box-shadow: var(--shadow-blue-soft);
  transition: transform var(--trans-medium), box-shadow var(--trans-medium);
}
.card:hover { transform: translateY(-6px); box-shadow: var(--shadow-blue-strong); }

/* small card */
.card.sm { padding:12px; border-radius: var(--radius-md); }

/* section headings */
.section-title { font-size: 20px; font-weight:700; color: #073248; margin-bottom: 10px; }
.card h3, .card h4 { margin:0 0 8px 0; color:#164556; }

/* =========================================================
   BUTTONS (PRIMARY / GHOST / DANGER)
========================================================= */
button, .btn {
  padding: 10px 16px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  color: white;
  font-weight: 700;
  background: linear-gradient(90deg, #6ec4ff, #0b74d4);
  box-shadow: 0 12px 36px rgba(80,150,255,0.16);
  transition: transform var(--trans-fast), box-shadow var(--trans-fast), opacity var(--trans-fast);
}
button:hover, .btn:hover { transform: translateY(-3px); box-shadow: 0 20px 55px rgba(80,150,255,0.22); }
.btn-ghost {
  background: transparent;
  border: 1px solid rgba(11,116,184,0.10);
  color: var(--primary-deep);
  box-shadow: none;
}
.btn-danger {
  background: linear-gradient(90deg,#ff7b7b,#ff4d4d);
  color: white;
}
button[disabled], .btn[disabled] { opacity: 0.5; cursor: not-allowed; transform: none; box-shadow:none; }

/* small button */
.btn-sm { padding: 8px 12px; border-radius: 8px; }

/* =========================================================
   INPUTS / FORMS
========================================================= */
input, select, textarea {
  width: 100%;
  padding: 10px 12px;
  border-radius: 8px;
  border: 1px solid rgba(150,190,255,0.28);
  background: linear-gradient(180deg,#ffffff,#f6fbff);
  transition: box-shadow var(--trans-fast), transform var(--trans-fast), border-color var(--trans-fast);
  font-size: 15px;
  color: #0f3644;
}
input:focus, select:focus, textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 10px 36px rgba(80,150,255,0.16);
  transform: translateY(-2px);
}
label { display:block; font-weight:600; color:#244452; margin-bottom:6px; font-size:13px; }
.help { font-size:13px; color:var(--muted); margin-top:6px; }

/* =========================================================
   SEARCH INPUT (special)
========================================================= */
.search-input {
  width: auto;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(150,190,255,0.28);
  background: linear-gradient(180deg,#fff,#f7fbff);
  font-size: 15px;
  transition: box-shadow var(--trans-fast), transform var(--trans-fast);
  margin-bottom: 20px;
}
.search-input:focus { box-shadow: 0 10px 34px rgba(80,150,255,0.16); transform: translateY(-2px); }

/* =========================================================
   ANALYTICS / STATS
========================================================= */
.analytics-stats {
  display:flex;
  gap: 32px;
  justify-content: center;
  flex-wrap:wrap;
  margin-top: 8px;
}
.stat-box {
  display:flex;
  flex-direction:column;
  align-items:center;
  gap:6px;
  padding: 12px 14px;
  width: 188px;
  border-radius: 12px;
  background: linear-gradient(180deg,#f9fdff,#eaf6ff);
  border: 1px solid rgba(150,190,255,0.32);
  box-shadow: var(--shadow-blue-soft);
  transition: transform var(--trans-fast), box-shadow var(--trans-fast), background var(--trans-fast);
}
.stat-box:hover { transform: translateY(-6px); box-shadow: var(--shadow-blue-strong); background: linear-gradient(180deg,#ffffff,#eef9ff); }
.stat-box h2 { margin:0; font-size:26px; color: var(--primary-deep); font-weight:700; }
.stat-box p { margin:0; color: var(--muted); font-size:13px; }
.stat-icon { width:24px; height:24px; color:var(--primary); }

/* =========================================================
   CHARTS
========================================================= */
.charts-row { display:flex; gap:20px; flex-wrap:wrap; margin-top:12px; align-items:stretch; }
.chart-box {
  padding:16px;
  border-radius:12px;
  background: linear-gradient(180deg,#f5fbff,#e9f6ff);
  border: 1px solid rgba(150,190,255,0.28);
  min-width:260px;
  width: calc(33% - 14px);
  box-shadow: var(--shadow-blue-soft);
  transition: transform var(--trans-fast), box-shadow var(--trans-fast);
}
.chart-box:hover { transform: translateY(-6px); box-shadow: var(--shadow-blue-strong); }
.chart-box h4 { margin:0 0 12px; color:#163d4b; font-size:15px; font-weight:600; }

/* responsive tweak for charts */
@media (max-width:1100px) {
  .chart-box { width:48%; }
}

/* =========================================================
   TABS
========================================================= */
.tabs-center { width:100%; display:flex; justify-content:center; margin-top:6px; }
.tabs { display:flex; gap:12px; flex-wrap:wrap; }
.tabs button {
  padding:10px 16px;
  background: linear-gradient(180deg,#f3f8ff,#eef7ff);
  border:1px solid rgba(150,190,255,0.18);
  border-radius:10px;
  cursor:pointer;
  transition: transform var(--trans-fast), box-shadow var(--trans-fast);
  font-weight:600;
  color:#123a4b;
}
.tabs button:hover { transform: translateY(-3px); box-shadow: var(--shadow-blue-soft); }
.tabs button.active { background: linear-gradient(90deg,var(--accent),#0b74d4); color:#fff; box-shadow: 0 10px 26px rgba(80,150,255,0.18); }

/* =========================================================
   CERTIFICATES SECTION
========================================================= */
.admin-container-vertical {
  max-width: 1200px;
  padding: var(--content-padding);
  margin: 10px auto;
  display:flex;
  flex-direction:column;
  gap:28px;
}
.certificates-section { margin-top: 6px; }
.scroll-box {
  max-height:520px;
  overflow-y:auto;
  padding-right:10px;
}
.scroll-box::-webkit-scrollbar { width:8px; }
.scroll-box::-webkit-scrollbar-thumb { background: linear-gradient(180deg,#bfe3ff,#9ecffb); border-radius:8px; }

/* certificate item */
.cert-item {
  background: linear-gradient(180deg,#ffffff,#f3fbff);
  border-radius:10px;
  padding:14px;
  border:1px solid rgba(150,190,255,0.22);
  margin-bottom:12px;
  transition: transform var(--trans-fast), box-shadow var(--trans-fast), background var(--trans-fast);
}
.cert-item:hover { transform: translateY(-6px); box-shadow: var(--shadow-blue-soft); background: linear-gradient(180deg,#ffffff,#eef9ff); }
.cert-item strong { color:#0b3a51; }
.cert-item small { color:var(--muted); font-size:13px; }

/* actions inside cert item */
.cert-item .actions { margin-top:8px; display:flex; gap:10px; }
.cert-item .actions button {
  padding:8px 12px;
  border-radius:8px;
  border:1px solid rgba(11,116,184,0.08);
  background:white;
  color:var(--primary-deep);
  font-weight:600;
  transition: transform var(--trans-fast), box-shadow var(--trans-fast);
}
.cert-item .actions button:hover { transform: translateY(-3px); box-shadow: var(--shadow-neutral); }
.cert-item .actions button.danger {
  background:linear-gradient(90deg,#ff8a8a,#ff6a6a); color:white; border:none;
}
.cert-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 4px;
  font-size: 15px;
}

.cert-icon {
  color: #007bff;
}

.cert-buttons {
  margin-top: 12px;
  display: flex;
  gap: 12px;
}

.edit-btn {
  background: #4fa3ff;
  color: white;
  padding: 8px 14px;
  border-radius: 6px;
  border: none;
}

.delete-btn {
  background: red;
  color: white;
  padding: 8px 14px;
  border-radius: 6px;
  border: none;
}

/* =========================================================
   MODALS (centered)
========================================================= */
/* =========================================================
   MODALS â€” Smooth POP-OUT animation
========================================================= */
/* === MODAL OVERLAY === */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;        /* VERY IMPORTANT */
}

/* === MODAL BOX === */
.modal {
  background: #fff;
  padding: 25px;
  border-radius: 12px;
  width: 400px;
  max-width: 90%;
  box-shadow: 0 10px 40px rgba(0,0,0,0.3);
  animation: pop 0.2s ease-out;
}

/* Animation (optional) */
@keyframes pop {
  from { transform: scale(0.8); opacity: 0; }
  to { transform: scale(1); opacity: 1; }
}

/* =========================================================
   AUTH PAGES (Login / Signup / Verify)
========================================================= */
.auth-card {
  width: auto;
  max-width: 470px;
  margin: 60px auto;
  background: linear-gradient(180deg,#ffffff,#f6fbff);
  border-radius:12px;
  padding:28px;
  border:1px solid rgba(150,190,255,0.22);
  box-shadow: 0 18px 55px rgba(80,150,255,0.12);
}
.auth-card h2 { color: #063b66; text-align:center; margin:0 0 8px; font-size:22px; }
.auth-card .subtext { color:var(--muted); text-align:center; margin-bottom:14px; }
.auth-actions { display:flex; gap:10px; margin-top:12px; }
.auth-actions .btn { flex:1; }
.auth-link { text-align:center; margin-top:12px; color:var(--primary-deep); font-weight:600; cursor:pointer; }

/* verify note */
.verify-note {
  background: linear-gradient(180deg,#eaf6ff,#f6fbff);
  border:1px solid rgba(150,190,255,0.18);
  padding:12px; border-radius:8px; color:var(--primary-deep); margin-bottom:12px;
}

/* =========================================================
   AUDIT LOGS
========================================================= */
.audit-container { max-width:1100px; margin:auto; padding:0 10px; display:flex; flex-direction:column; gap:10px; }
.audit-card { padding:26px; border-radius:12px; background:linear-gradient(180deg,#ffffff,#f6fbff); border:1px solid rgba(150,190,255,0.12); box-shadow: var(--shadow-neutral); }
.log-item { padding:12px; border-radius:10px; background:white; border:1px solid rgba(150,190,255,0.08); margin-bottom:10px; transition:transform var(--trans-fast), box-shadow var(--trans-fast); }
.log-item:hover { transform: translateY(-3px); box-shadow: var(--shadow-neutral); }
.log-action { background: linear-gradient(90deg,#e6f7ff,#dff4ff); color:var(--primary-deep); padding:6px 10px; border-radius:8px; font-weight:700; font-size:13px; }

/* =========================================================
   FOOTER (GLASSY SKY)
========================================================= */
.footer {
  background: linear-gradient(180deg,#f6fbff,#eaf6ff);
  padding: 34px 20px 20px;
  text-align:center;
  color:#123a4b;
  margin-top:auto;
  border-top:1px solid rgba(150,190,255,0.12);
  box-shadow: 0 -6px 20px rgba(80,150,255,0.04);
}
.footer-links { display:flex; gap:16px; justify-content:center; margin-top:10px; }
.footer-links a { color:#0e5f83; font-weight:600; }
.footer-links a:hover { color:var(--primary-deep); }
.footer-bottom { margin-top:10px; color:#4b6b7a; font-size:13px; }

/* =========================================================
   RESPONSIVE
========================================================= */
@media (max-width: 1100px) {
  .chart-box { width: 48%; }
  .search-input { width: 260px; }
  .stat-box { width: 160px; }
  .container { padding:18px; }
}
@media (max-width: 800px) {
  .chart-box { width:100%; }
  .nav-links { display:none; } /* add burger later if needed */
  .auth-card { width:100%; padding:20px; margin: 30px auto; }
  .search-input { width:100%; }
  .analytics-stats { gap:12px; justify-content:center; }
  .header { padding: 10px 14px; gap:10px; }
}
@media (max-width:420px) {
  .stat-box h2 { font-size:22px; }
  .search-input { padding:10px; }
  .cert-item { padding:12px; }
}

/* =========================================================
   SMALL HELPERS & ACCESSIBILITY
========================================================= */
.sr-only { position:absolute !important; width:1px; height:1px; padding:0; margin:-1px; overflow:hidden; clip:rect(0,0,0,0); white-space:nowrap; border:0; }
.badge { padding:4px 8px; border-radius:8px; font-weight:700; font-size:12px; background: rgba(11,116,184,0.08); color:var(--primary-deep); }

/* END OF STYLESHEET */

